@font-face {
  font-family: 'Proxima Nova';
  src: url("../fonts/ProximaNova-Regular/ProximaNova-Regular.eot");
  src: url("../fonts/ProximaNova-Regular/ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Regular/ProximaNova-Regular.woff") format("woff"), url("../fonts/ProximaNova-Regular/ProximaNova-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url("../fonts/ProximaNova-Light/ProximaNova-Light.eot");
  src: url("../fonts/ProximaNova-Light/ProximaNova-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Light/ProximaNova-Light.woff") format("woff"), url("../fonts/ProximaNova-Light/ProximaNova-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url("../fonts/ProximaNova-Thin/ProximaNova-Thin.eot");
  src: url("../fonts/ProximaNova-Thin/ProximaNova-Thin.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Thin/ProximaNova-Thin.woff") format("woff"), url("../fonts/ProximaNova-Thin/ProximaNova-Thin.ttf") format("truetype");
  font-weight: 100;
  font-style: normal;
}

body {
  color: #ffffff;
  font-size: 16px;
  font-family: "Proxima Nova", sans-serif;
  font-weight: normal;
  line-height: 1.3;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  position: relative;
  min-width: 320px;
  background-color: #ffffff;
  overflow-x: hidden;
}

* {
  margin: 0;
  padding: 0;
}

a,
a:hover {
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media only screen and (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media only screen and (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

.content-wrapper {
  position: relative;
  display: flex;
  min-height: 100vh;
  background-color: #0f1320;
}

.content-wrapper::after {
  position: absolute;
  display: block;
  content: '';
  top: 0;
  left: 99%;
  background-color: #0f1320;
  width: 100vw;
  height: 100%;
}

.main-content {
  height: 100vh;
  flex-grow: 1;
  overflow: hidden;
}

.site-nav {
  position: relative;
  display: flex;
  width: 32%;
  min-width: 320px;
  min-height: 100vh;
  margin-right: 65px;
  align-items: center;
  flex-direction: column;
  background-color: #16171b;
}

.site-nav::before {
  position: absolute;
  display: block;
  content: '';
  top: 0;
  right: 99%;
  height: 100%;
  width: 100vw;
  background-color: #16171b;
}

.site-nav .avatar {
  width: 150px;
  height: auto;
  margin: 15% 0 60px;
}

.main-menu {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 55%;
  max-height: 450px;
  margin-bottom: 65px;
}

.main-menu__item {
  height: 25%;
}

.main-menu__link {
  display: flex;
  align-items: center;
  height: 100%;
  padding-left: 65px;
  border-left: 12px solid transparent;
  color: #ffffff;
  font-size: 2.025rem;
  font-weight: 300;
  transition: all 0.3s ease;
}

.main-menu__link:hover {
  background-color: #1b1c1f;
}

.main-menu__link.active {
  border-color: #4ec818;
  color: #4ec818;
}

.btn,
.button {
  padding: 15px 25px;
  border: none;
  background-color: #1d212d;
  transition: background-color 0.3s ease;
  color: #ffffff;
  font-size: 1.75rem;
  font-weight: 300;
  cursor: pointer;
}

.btn:hover,
.button:hover {
  background-color: #4ec818;
}

.section-title {
  display: inline-block;
  margin: 35px 0 50px;
  padding-bottom: 8px;
  border-bottom: 4px solid #4ec818;
  font-size: 2.7rem;
  font-weight: 300;
}

.about-section {
  position: relative;
  width: 100%;
}

.about-section::after {
  position: absolute;
  display: block;
  content: '';
  right: 0;
  bottom: -10px;
  width: 50%;
  height: 100%;
  background-image: url("../img/about-bg.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 200%;
  opacity: 0.05;
}

.about-section .my-name {
  font-size: 4.6875rem;
  font-weight: 300;
}

.about-section .my-name span {
  color: #4ec818;
  font-weight: normal;
}

.about-section .my-position {
  margin-bottom: 40px;
  color: #4ec818;
  font-size: 2.7rem;
}

.about-section .my-hobbies {
  width: 75%;
  margin-bottom: 20px;
  font-size: 2.25rem;
  font-weight: 100;
  opacity: 0.58;
}

.about-section .buttons {
  display: flex;
  justify-content: space-between;
  width: 55%;
}

.works-section {
  width: 100%;
  height: 100%;
  padding-right: 17px;
  overflow-y: scroll;
}

.works-list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -70px;
}

.works-item {
  margin-bottom: 50px;
  width: 45%;
  margin-right: 5%;
}

.works-item:nth-child(2n) {
  margin-right: 0;
}

.works-item:last-child, .works-item:nth-last-child(2) {
  margin-bottom: 0;
}

.works-item:hover .works-item__img {
  transform: scale(0.8);
}

.works-item:hover .works-item__title {
  top: -100%;
}

.works-item:hover .works-item__tech {
  top: 0;
}

.works-item__img {
  width: 100%;
  height: 100%;
  transition: transform 0.3s ease;
}

.works-item__img-wrapper {
  height: 155px;
  margin-bottom: 15px;
  background-color: #353055;
}

.works-item__desc {
  position: relative;
  height: 26px;
  overflow: hidden;
  color: #ffffff;
  font-size: 1.625rem;
}

.works-item__title {
  position: absolute;
  top: 0;
  font-weight: 300;
  transition: top 0.3s ease;
}

.works-item__tech {
  position: absolute;
  top: 100%;
  font-weight: 300;
  transition: top 0.3s ease;
}

.skills-section {
  width: 100%;
  height: 100%;
  padding-right: 17px;
  overflow-y: scroll;
}

.skills-subsection {
  margin-bottom: 50px;
  padding: 30px 20px;
  background-color: #2b2a44;
}

.skills-subsection__title {
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px solid #4ec818;
  font-size: 1.625rem;
  font-weight: 300;
}

.skills-list {
  display: flex;
  align-items: center;
}

.skills-list .skills-item {
  width: 25%;
  margin-right: 50px;
}

.skills-list .skills-item:last-child {
  margin-right: 0;
}

.skills-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skills-item:hover .skills-item__img {
  filter: grayscale(0%);
}

.skills-item:hover .skills-item__desc {
  color: #2b2a44;
}

.skills-item:hover .skills-item__desc::before {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.skills-item:hover .skills-item__desc::after {
  left: 100%;
}

.skills-item__img {
  max-width: 100%;
  max-height: 150px;
  filter: grayscale(100%);
  transition: filter 0.5s ease;
}

.skills-item__img-wrapper {
  display: flex;
  width: 100%;
  height: 200px;
  align-items: center;
  justify-content: center;
}

.skills-item__desc {
  position: relative;
  display: block;
  width: 100%;
  text-align: center;
  overflow: hidden;
  transition: color 0.4s ease;
  font-size: 1.125rem;
}

.skills-item__desc::before {
  position: absolute;
  display: block;
  content: '';
  left: -100%;
  content: attr(desc);
  transition: all 0.2s ease;
  color: #ffffff;
}

.skills-item__desc::after {
  position: absolute;
  display: block;
  content: '';
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background-color: #4ec818;
  transition: left 0.5s ease;
}

.contacts-list__item {
  margin-bottom: 50px;
}

.contacts-list__title {
  display: inline-block;
  margin-right: 50px;
  font-size: 2rem;
  font-weight: 300;
}

.contacts-list__link {
  position: relative;
  font-size: 1.75rem;
  color: #ffffff;
  opacity: 0.7;
  transition: opacity 0.3s ease;
}

.contacts-list__link::after {
  position: absolute;
  display: block;
  content: '';
  left: 0;
  bottom: -5px;
  width: 0;
  height: 4px;
  background-color: #4ec818;
  transition: width 0.3s ease;
}

.contacts-list__link:hover {
  opacity: 1;
}

.contacts-list__link:hover::after {
  width: 100%;
}

.contacts-list span {
  margin: 0 10px;
  opacity: 0.7;
  font-size: 1.625rem;
}
